:root {
  --darkBlue: #36498a;
  --mediumBlue: #a5b5e5;
  --lightBlue: #f5f7ff;
  --gray: #333333;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main {
  font-family: acumin-pro, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  display: grid;
  grid-template-columns:
    [full-start] minmax(1rem, 1fr)
    [main-start] minmax(0, 60rem)
    [main-end] minmax(1em, 1fr)
    [full-end] minmax(0rem, 0);
}

.header {
  grid-column: full;
  color: white;
  font-family: acumin-pro, sans-serif;
  background-color: #36498a;
  width: 100%;
  padding-top: 2vh;
  padding-bottom: 2vh;
  padding-left: 7vw;
}

.logo{
  height: 5vh;
  fill: white;
}

main > * {
  grid-column: main;
}

code {
}

.MuiDataGrid-root {
  white-space: normal !important;
}

.MuiDataGrid-cell {
  line-height: normal !important;
  overflow: auto !important;
  white-space: normal !important;
  padding: 1rem !important;
}

.footer {
  font-family: acumin-pro, sans-serif;
  color: #535353;

  background-color: #f2f2f2;
  padding: 3rem;
  width: 100%;
  margin-top: 5rem;
}

.footer h2{
  font-size: 1.6rem;
  font-weight: 400;
}

.buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button {
  color: white;
  background-color: #36498a;
  border-radius: 2px;
  border: 0;
  font-size: 1rem;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.button:first-child {
  margin-top: 0.5rem;
}

.detail-container {
  display: flex;
  padding-top: 2rem;
}

.headline {
  margin-bottom: 1rem;
  font-weight: 100;
}

.search {
  width: 32rem;
  padding-left: 0rem;
  padding-right: 1rem;
  padding-bottom: 1rem;
  margin-bottom: 1rem;
}

.searchbar {
  width: 39rem;
  padding: 1.2rem;
  border: 2px;
  margin-bottom: 2rem;
  background-color: var(--lightBlue);
}

::placeholder {
  color: black;
  opacity: 0.5;
  font-size: 1rem;
}

