.modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  /* top: 50%; */
}

.modal-elements {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* flex-direction: row; */
  background-color: white;
  width: 500px;
  height: 400px;
  justify-content: space-between;
}

.modal-elements > h1 {
  font-size: 16px;
}

.modal-elements > label,
h2 {
  font-size: 14px;
}

.modal-elements > input {
  border: 2px solid #4dbfac;
  border-radius: 6px;
  width: 70%;
  padding: 0.5rem;
}

.modal-elements button {
  padding: 1rem;
  font-size: 1rem;
  padding: 1rem 2rem;
  background-color: white;
  color: #828282;
  border: 2px solid #828282;
}
.modal-elements button:first-child {
  /* margin-top: 1rem; */
  background-color: #4dbfac;
  border: 2px solid #4dbfac;
  margin-right: 0.5rem;
  color: white;
}
